html, body {
  height: 100%;
  margin: 0;
}

.mainContent {
  min-height: 100%;
}

.tic-tac {
  /* width: 160px;
  height: 160px; */

  width: 26vw;
  height: 26vw;

  /* border: 2px solid black; */
  margin: 10px auto;
}

.lineButtons {
  margin-bottom: 10px;
  display: flex;
  justify-content: space-around;
}

.counter {
  text-align: center;
}

.tt-grid {
  /* width: 50px;
  height: 50px; */
  width: 8vw;
  height: 8vw;
  border: 1px solid black;
  float: left;
  box-sizing: border-box;

  text-align: center;
  font-size: 6vw;
}

.logo {
  height: 5vw;
}

a .github {
  font-size: 20px;
  text-decoration: none;
}

.githubSrc {
  text-decoration: none;
}

.footer {
  padding: 10px;
  background-color: cadetblue;
  display: flex;
  justify-content: space-around;
}